body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background: linear-gradient(to right, #f0a2ff, #d7a6f1, #f8d2fc);
    ;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}
@font-face {
    font-family: 'Dubstep Dungeons Regular';
    font-style: normal;
    font-weight: normal;
    src: local('Dubstep Dungeons Regular'), url("/robofriends/static/media/Dubstep Dungeons.ec97511a.woff") format('woff');
}

h1 {
    font-family: 'Dubstep Dungeons Regular';
    font-weight: 200;
    color: white;
    letter-spacing: 1rem;
}
